<template>
    <div>
        <h1>This is LOGOUT.vue</h1>
        <h2>You have been successfully logged out</h2>
    </div>
</template>

<script>
    import store from '../store'

    export default {
        name: "Logout",
        mounted() {
            this.onLogout();
        },
        methods: {
            onLogout() {
                store.dispatch('logout')
                    .then(() => {
                        this.$router.push({name: 'home'})
                    })
                    .catch(() => {
                        this.$router.push({name: 'home'})
                    })
            }

        }
    }
</script>

